import React, { Component } from 'react'
import Workbook from 'react-excel-workbook'
import { Icon } from 'antd'
import 'antd/dist/antd.css'
const columns = [
    {
        date: " 2019-01-23",
        number: "820079564894",
        price: "970",
        nameCustomer: "ชื่อลูกค้า",
        address: "ที่อยู่จัดส่งพัสดุ",
        post: "99999",
        phone: "080000007"
    }
]
class exportExcel extends Component {

    render() {
        return (
            <div>
                <Workbook filename="COD.xlsx" element={<img src={"/excel.png"} />}>
                    <Workbook.Sheet data={columns} name="Sheet A">
                        <Workbook.Column label="date" value="date" />
                        <Workbook.Column label="เลขพัสดุ" value="number" />
                        <Workbook.Column label="ราคาสินค้า" value="nameCustomer" />
                        <Workbook.Column label="ชื่อลูกค้า" value="address" />
                        <Workbook.Column label="ที่อยู่จัดส่งพัสดุ" value="date" />
                        <Workbook.Column label="รหัสไปรษณี" value="post" />
                        <Workbook.Column label="เบอร์ติดต่อ" value="phone" />
                    </Workbook.Sheet>
                </Workbook>
            </div>
        )
    }
}

export default exportExcel